import {request} from "../axios/request";

const baseUrl = '/Image'

const ImageApi = {}

ImageApi.upload = image => {
  return request({
    url: `${baseUrl}/upload`,
    method: 'post',
    data: image
  })
}

ImageApi.loadAll = () => {
  return request({
    url: 'get',
    method: 'get'
  })
}

ImageApi.query = keyword => {
  return request({
    url: `${baseUrl}/query`,
    method: 'get',
    params: {
      keyword
    }
  })
}

ImageApi.delete = id => {
  return request({
    url: `${baseUrl}/delete`,
    method: 'get',
    params: {
      id
    }
  })
}

export default ImageApi