<template>
  <div class="lwj-container">
    <a-card>
      <a-row class="cards" type="flex" :gutter="16">
        <a-col :span="6" :order="1">
          <div style="float: left; margin-right: 10px;padding-top: 4px">关键词 :</div> <a-input style="width: 75%" />
        </a-col>
        <a-col :span="4" :order="2">
          <a-button style="margin-right: 10px" type="primary">查询</a-button>
          <a-button>重置</a-button>
        </a-col>
      </a-row>
      <a-row class="mr-tt">
        <a-button type="primary" icon="cloud-download">上传</a-button>
      </a-row>
    </a-card>

    <a-row class="mr-t">
      <a-list size="small" :grid="{ gutter: 16, column: 6 }" :pagination="pagination" :data-source="images">
        <a-list-item class="imag" slot="renderItem" slot-scope="item">
          <a-card hoverable>
            <img
              style="height: 100px"
              slot="cover"
              :src="item.photo"
              :key="item.id"
            />
          </a-card>
        </a-list-item>
      </a-list>
    </a-row>
  </div>
</template>

<script>
  import ImageApi from "../../api/Images";
  export default {
    name: "Images",
    data() {
      return {
        images: [],
        pagination: {
          pageSize: 18
        }
      };
    },
    created () {
      this.loadAll()
    },
    methods: {
      async loadAll() {
        ImageApi.loadAll().then(res => {
          if (res.code === 1000) {
            this.images = res.data.images
          }
        }).catch(err => {
          this.$message.error(err.toString())
        })
      }
    },
  }
</script>

<style lang="less" scoped>
  @media screen and (max-width: 960px) {
    .cards > * {
      width: 100%;
      margin: 10px;
    }
    .mr-tt > * {
      margin-left: 10px;
      margin-top: 0!important;
    }
    .imag {
      width: 50%;
    }
  }
</style>